import React from "react";
import bg from './bgc-contact.png'
import Footer from "./Footer";

import "./footer.css";
const Contact = () => {
  return (
    <>
      <div className="contact-main">
        <div className="news-header">
          <h1>CONTACT-US</h1>
        </div>
        <hr />
  
        <div className="contact-page">
          <div className="c-page-1">
            <img src={bg} alt="" />
          </div>
          <div className="c-page-2">
            Name
            <input
              type="text"
              className="text"
              placeholder="Your Name"
              required="required"
            />
            Email
            <input type="email" className="text" placeholder="Your Email" />
            Phone Number
            <input type="number" className="text" placeholder="Your Number" />
            Comment
            <textarea
              name="Message"
              className="text"
              id="message"
              cols="50"
              rows="7"
              placeholder="Your Comment Here"
            ></textarea>
            <a href="#" className="btn btn-primary my-3 submit">
              Submit
            </a>
          </div>
        </div>
      </div>
      <Footer/>
    </>
  );
};

export default Contact;
