import React from "react";
import "./header.css";
import my_pic from "./my-photos1.jpg";
import { NavLink } from "react-router-dom";
import Home from "../../Home";
export const Header = () => {
  return (
  
    <div className="header">
      <div className="logo">
        <NavLink to="/">
        <img src={my_pic} alt="Logo-pic" />
        </NavLink>
      </div>
      <nav className="navbar">
        <ul>
          <li>
            <NavLink to="/profile" className="list ">
            <i class="bi bi-person-circle"></i> Profile
            </NavLink>
          </li>
          <li>
            <NavLink to="/project" className="list ">
            <i class="bi bi-boxes"></i> Project
            </NavLink>
          </li>
          <li>
            <NavLink to="/skill" className="list ">
            <i class="bi bi-pencil-square"></i> Skill
            </NavLink>
          </li>
          <li>
            <NavLink to="/social" className="list ">
            <i class="bi bi-slack"></i> Social
            </NavLink>
          </li>
          <li>
            <NavLink to="/contact" className="list ">
            <i class="bi bi-telephone-forward-fill"></i> Contact
            </NavLink>
          </li>
        </ul>
      </nav>
      <div className="right-header">
        <a href="mailto:www.mdmozammil@gmail.com">
          <i class="bi bi-envelope-check-fill"></i>
        </a>
      </div>
      <div className="right-collapse">
        <button
          className="btn"
          type="button"
          data-bs-toggle="offcanvas"
          data-bs-target="#offcanvasWithBothOptions"
          aria-controls="offcanvasWithBothOptions"
        >
          <i className="fa-solid fa-bars"></i>
        </button>

        <div
          className="offcanvas offcanvas-start"
          data-bs-scroll="true"
          tabindex="-1"
          id="offcanvasWithBothOptions"
          aria-labelledby="offcanvasWithBothOptionsLabel"
        >
          <div className="offcanvas-header">
            <h5 className="offcanvas-title" id="offcanvasWithBothOptionsLabel">
            <div className="logo">
        <NavLink to="/">
        <img src={my_pic} alt="Logo-pic" />
        </NavLink>
      </div>
            </h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="offcanvas"
              aria-label="Close"
            ></button>
          </div>
          <div className="offcanvas-body collapse-navbar">
            <ul>
              <li>
                <NavLink to="/profile" className="list">
                  Profile
                </NavLink>
              </li>
              <li>
                <NavLink to="/project" className="list">
                Project
                </NavLink>
              </li>
              <li>
                <NavLink to="/skill" className="list">
                Skill
                </NavLink>
              </li>
              <li>
                <NavLink to="/social" className="list">
                Social
                </NavLink>
              </li>
              <li>
                <NavLink to="/contact" className="list">
                Contact
                </NavLink>
              </li>
              <li className="resume">
                <a href={my_pic} type="download" download={my_pic}>
                  Download CV <i class="bi bi-cloud-arrow-down-fill"></i>
                </a>
              </li>
            </ul>

            <div className="collapse-social-link">
              <div className="col">
                <a href="https://www.facebook.com/mdmozammil01" target="_blank">
                  <i class="bi bi-facebook"></i>
                </a>
                <a href="https://twitter.com/MozammiI"target="_blank">
                  <i class="bi bi-twitter"></i>
                </a>
                <a href="https://www.instagram.com/_muzzu_0/" target="_blank">
                  <i class="bi bi-instagram"></i>
                </a>
                <a href="https://www.linkedin.com/in/md-mozammil-2a6030159?lipi=urn%3Ali%3Apage%3Ad_flagship3_profile_view_base_contact_details%3BGrRjlFPxQ3a8ir7WFHirgg%3D%3D" target="_blank">
                  <i class="bi bi-linkedin"></i>
                </a>
                <a href="https://github.com/MuzzammiI" target="_blank">
                  <i class="bi bi-github"></i>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
