// import React from 'react'
import React, { useState } from "react";
const Project_1 = (props) => {
    const changeupcase = () => {
        // console.log("uppercase");
        let newtext = text.toUpperCase();
        setText(newtext);
      };
      const changeLowcase = () => {
        // console.log("lowercase");
        let newtext = text.toLowerCase();
        setText(newtext);
      };
      const Capital = () => {
        // console.log("lowercase");
        // str.charAt(0).toUpperCase() + str.slice(1);
     
        let newtext = text.charAt(0).toUpperCase()+text.slice(1);
        setText(newtext);
      };
      const Reverse = () => {
        // console.log("lowercase")
        let newtext = text.reverse();
        setText(newtext);
      };
      const Clear = () => {
        // console.log("lowercase");
        let newtext = '';
        setText(newtext);
      };
    
      const activechange = (event) => {
        setText(event.target.value);
      };
      const [text, setText] = useState("Enter the text here");
  return (
   <>
     <div className="comment">
      <h1>Enter the text for Any manipulate Action</h1>
      <textarea
        name="text"
        id=""
        cols="80"
        rows="10"
        value={text}
        onChange={activechange}
        ></textarea>
    </div>
    <div className="btn-box">

       <div className="btn btn-primary" onClick={changeupcase}>
       UPPERCASE
     </div>
     <div className="btn btn-primary" onClick={changeLowcase}>
       LOWERCASE
     </div>
     <div className="btn btn-primary" onClick={Capital}>
       CAPITAL-LETTER
     </div>
     <div className="btn btn-primary" onClick={Reverse}>
       REVERSE
     </div>
     <div className="btn btn-primary" onClick={Clear}>
       CLEAR
     </div>
    </div>
    <div className="text-summary">
      <h1>Your Text summary</h1>
      <p>{text.split(" ").length} words and {text.length} Characters</p>
      <p>{ 0.008 * text.split(" ").length } Minutes Read</p>
      <h3>Preview</h3>
      <p>{text}</p>
    </div>
    
        </>
  )
}

export default Project_1;